import React, { useState } from 'react';
import { Container, Form, Button } from 'react-bootstrap';
import { useMutation } from 'react-query';
import axios from 'axios';
import 'bootstrap/dist/css/bootstrap.min.css';

const RegistrationForm = () => {
    const [formData, setFormData] = useState({});
    const mutation = useMutation(formData =>
        axios.post('https://sheet.best/api/sheets/b0fd887a-bd72-4263-b98e-16d1753bb388', formData)
    );

    const handleSubmit = (e) => {
        e.preventDefault();
        mutation.mutate(formData);
    };

    const handleChange = (e) => {
        const { name, value } = e.target;
        setFormData({
            ...formData,
            [name]: value
        });
    };

    return (
        <Container className="mt-5 card pt-2 pb-3" style={{ backgroundColor: '#f27f24', color: 'white' }}>
            <h2 className='fw-light text-start '>5 SUT U I HC PH 50%</h2>
            <h2 className='fw-semibold text-start '>ng k xt tuyn ngay</h2>
            <Form className="fw-bold" onSubmit={handleSubmit}>
                <Form.Group controlId="fullName">
                    <Form.Label>H v tn</Form.Label>
                    <Form.Control
                        type="text"
                        placeholder="Nhp h v tn"
                        name="fullName"
                        value={formData.fullName || ''}
                        onChange={handleChange}
                        required
                    />
                </Form.Group>

                <Form.Group controlId="email">
                    <Form.Label>Email</Form.Label>
                    <Form.Control
                        type="email"
                        placeholder="Nhp email"
                        name="email"
                        value={formData.email || ''}
                        onChange={handleChange}
                        required
                    />
                </Form.Group>

                <Form.Group controlId="phone">
                    <Form.Label>S in thoi</Form.Label>
                    <Form.Control
                        type="tel"
                        placeholder="Nhp s in thoi"
                        name="phone"
                        value={formData.phone || ''}
                        onChange={handleChange}
                        required
                    />
                </Form.Group>

                <Form.Group controlId="registrationType">
                    <Form.Label>i tng ng k</Form.Label>
                    <Form.Control
                        as="select"
                        name="registrationType"
                        value={formData.registrationType || ''}
                        onChange={handleChange}
                        required
                    >
                        <option value="">Chn i tng ng k</option>
                        <option value="graduated_student">Hc sinh tt nghip</option>
                        <option value="grade_12_student">Lp 12</option>
                        <option value="university_student">Sinh vin</option>
                        <option value="working_person">Ngi i lm</option>
                    </Form.Control>
                </Form.Group>

                <Form.Group controlId="selectedSubject">
                    <Form.Label>Chn mn hc</Form.Label>
                    <Form.Control
                        as="select"
                        name="selectedSubject"
                        value={formData.selectedSubject || ''}
                        onChange={handleChange}
                        required
                    >
                        <option value="">Chn mn hc</option>
                        <option value="network_security">An ninh mng</option>
                        <option value="software_development">Pht trin phn mm</option>
                        <option value="graphic_design">Thit k  ho</option>
                        <option value="ces">CES</option>
                        <option value="whs_cloud_foundation">WHS Cloud Foundation</option>
                    </Form.Control>
                </Form.Group>

                <Button variant="primary" type="submit" className='mt-3 fw-semibold' style={{ backgroundColor: '#9B3922', color: 'white', border: 'none' }} disabled={mutation.isLoading}>
                    {mutation.isLoading ? 'ang gi...' : 'ng K'}
                </Button>
                {mutation.isError && <div style={{ color: 'red', marginTop: '10px' }}>C li xy ra khi gi d liu. Vui lng th li sau.</div>}
            </Form>
        </Container>
    );
};

export default RegistrationForm;
